import React from 'react';
import PropTypes from 'prop-types';
import { injectIntl, intlShape } from 'react-intl';
import moment from 'moment';
import parsers from '../../../helpers/parsers';

const { parseSubjectTypes } = parsers;

/**
 * TODO:
 * - handle error if no data
 * - translations
 * - prop-types
 * - add keys in loops
 * - submit button
 * - localSchedule
 */

const Week = ({ type, children, className }) => {
  return (
    <div className={className}>
      <h3 className="form__week-title">{type} </h3>
      {children}
    </div>
  );
};

const Day = ({
  children,
  index,
  weekType,
  day,
  lang,
}) => (
  <div className="form__group" key={index}>
    <fieldset className="form__fieldset">
      <legend>
        {moment().locale(lang).day(index + 1).format('dddd')}
      </legend>
      {children}
    </fieldset>
  </div>
);

const Pair = ({
  index,
  weekType,
  weekDay,
  intl,
  teachers,
  subjects,
  scheduleList,
  onChangeScheduleItem,
}) => {
  const { formatMessage } = intl;

  const isoWeekDay = weekDay + 1;
  const pairNumber = index + 1;

  let currentPairIndex = undefined;

  const currentPair = scheduleList && scheduleList.filter((item, number) => {
    if (item.weekType === weekType && item.weekDay === isoWeekDay && item.lesson === pairNumber) {
      currentPairIndex = number;

      return item;
    }
  });

  return (
    <div className="form__pair" key={index}>
      <h3 className="form__pair-title">{pairNumber} </h3>

      <label className="form__label" htmlFor={`odd-${pairNumber}-teachers`}>
         :
        <select
          id={`odd-${pairNumber}-teachers`}
          name="teacherId"
          className="form__select"
          value={(currentPair && currentPair.length && currentPair[0].teacherId) || ''}
          onChange={(e) => onChangeScheduleItem(e, isoWeekDay, weekType, pairNumber, currentPair[0], currentPairIndex)}
        >
          {teachers && teachers.list && teachers.list.map((teacher) => (
            <option
              key={teacher.id}
              value={teacher.id}
            >
              {teacher.name}
            </option>
          ))}
        </select>
      </label>

      <label className="form__label" htmlFor={`${pairNumber}-subjects`}>
         :
        <select
          id={`${pairNumber}-subjects`}
          name="subjectId"
          className="form__select"
          value={(currentPair && currentPair.length && currentPair[0].subjectId) || ''}
          onChange={(e) => onChangeScheduleItem(e, isoWeekDay, weekType, pairNumber, currentPair[0], currentPairIndex)}
        >
          {subjects && subjects.list && subjects.list.map((subject) => (
            <option
              key={subject.id}
              value={subject.id}
            >
              {subject.name} {formatMessage({ id: parseSubjectTypes(subject.type) }).toLowerCase()}
            </option>
          ))}
        </select>
      </label>

      <label className="form__label" htmlFor={`${pairNumber}-location`}>
          
        <input
          id={`${pairNumber}-location`}
          name="location"
          className="form__input"
          value={(currentPair && currentPair.length && currentPair[0].location) || ''}
          onChange={(e) => onChangeScheduleItem(e, isoWeekDay, weekType, pairNumber, currentPair[0], currentPairIndex)}
          placeholder=": 2215"
        />
      </label>
    </div>
  );
};

const ScheduleForm = ({
  intl,
  lang,
  submitted,
  onChangeScheduleItem,
  onChangeGroupName,
  onChangeSemester,
  onSubmit,
  group,
  teachers,
  semesters,
  semester,
  subjects,
  schedule,
  scheduleList,
}) => {
  const { formatMessage } = intl;

  return (
    <form className="form" onSubmit={onSubmit}>
      <div className="row">
        <div className="col-xs-12">
          <input
            id="groupName"
            name="groupName"
            value={(group && group.name) || ''}
            className="form__input"
            onChange={onChangeGroupName}
          />
        </div>

        <div className="col-xs-12">
          <label className="form__label" htmlFor="semester">
             :
            <select
              id="semester"
              name="semester"
              value={semester || ''}
              className="form__select"
              onChange={onChangeSemester}
            >
              {semesters.list && semesters.list.map((_semester) => (
                <option
                  key={_semester.id}
                  value={_semester.id}
                >
                  {_semester.number}
                </option>
              ))}
            </select>
          </label>
        </div>

        <Week
          className="form__week col-xs-12 col-md-6"
          type={1}
        >
          {[...Array(5)].map((d, i) => (
            <Day
              index={i}
              lang={lang}
              weekType={1}
            >
              {[...Array(5)].map((p, j) => (
                <Pair
                  index={j}
                  weekType={1}
                  weekDay={i}
                  intl={intl}
                  teachers={teachers}
                  subjects={subjects}
                  scheduleList={scheduleList}
                  onChangeScheduleItem={onChangeScheduleItem}
                />
              ))}
            </Day>
          ))}
        </Week>

        <Week
          className="form__week col-xs-12 col-md-6"
          type={2}
        >
          {[...Array(5)].map((d, i) => (
            <Day
              index={i}
              lang={lang}
              weekType={2}
            >
              {[...Array(5)].map((p, j) => (
                <Pair
                  index={j}
                  weekDay={i}
                  weekType={2}
                  intl={intl}
                  teachers={teachers}
                  subjects={subjects}
                  scheduleList={scheduleList}
                  onChangeScheduleItem={onChangeScheduleItem}
                />
              ))}
            </Day>
          ))}
        </Week>
      </div>
    </form>
  );
};

ScheduleForm.propTypes = {
  intl: intlShape.isRequired,
  lang: PropTypes.string.isRequired,
  submitted: PropTypes.bool,
  onChangeScheduleItem: PropTypes.func.isRequired,
  onChangeSemester: PropTypes.func.isRequired,
  onChangeGroupName: PropTypes.func.isRequired,
  onSubmit: PropTypes.func.isRequired,
  group: PropTypes.shape({
    id: PropTypes.number,
    name: PropTypes.oneOfType([
      PropTypes.number,
      PropTypes.string,
    ]),
  }),
  semesters: PropTypes.shape({
    fetching: PropTypes.bool,
    list: PropTypes.arrayOf(PropTypes.object),
  }),
  teachers: PropTypes.shape({
    fetching: PropTypes.bool,
    list: PropTypes.arrayOf(PropTypes.object),
  }),
  subjects: PropTypes.shape({
    fetching: PropTypes.bool,
    list: PropTypes.arrayOf(PropTypes.object),
  }),
  semester: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.number,
  ]),
  schedule: PropTypes.shape({
    fetching: PropTypes.bool,
    list: PropTypes.arrayOf(PropTypes.object),
  }),
};

ScheduleForm.defaultProps = {
  submitted: false,
  group: {},
  semesters: {},
  schedule: {},
  teachers: {},
  subjects: {},
  semester: '',
};

export default injectIntl(ScheduleForm);
